<style lang="less">
	.cloumn{
		background:#f66
	}
</style>
<template>
	<div>
	 	<el-date-picker
	      	v-model="value"
	      	ref="dddd"
	      	type="datetimerange"
	      	placeholder="选择时间范围">
    	</el-date-picker>
	    	<el-table :data="tableData" border style="width: 100%">
		    <el-table-column style="background:#f66"  prop="date" label="日期" width="180">
		    	<template scope="scope">
		    		<el-checkbox v-model="status[scope.$index]">备选项</el-checkbox>{{scope.row.address}}
		    		{{scope.$index}}
		    	</template>
		    </el-table-column>
		    <el-table-column prop="name" label="姓名" width="180">
		    </el-table-column>
		    <el-table-column prop="address" label="地址">
		    </el-table-column>
	  	</el-table>
		<el-time-picker
			v-model="value1"
			:picker-options="{
		  	selectableRange: '00:00 - 23:59'
			}"
			placeholder="任意时间点">
		</el-time-picker>
	</div>
</template>
<script type="text/javascript">
	export default{
		data(){
			return {
				value:'',
				value1:'',
				tableData: [{
		          date: '2016-05-02',
		          name: '王小虎',
		          address: '上海市普陀区金沙江路 1518 弄',
		          status:false
		        }, {
		          date: '2016-05-04',
		          name: '王小虎',
		          address: '上海市普陀区金沙江路 1517 弄',
		          status:true
		        }, {
		          date: '2016-05-01',
		          name: '王小虎',
		          address: '上海市普陀区金沙江路 1519 弄',
		          status:true
		        }, {
		          date: '2016-05-03',
		          name: '王小虎',
		          address: '上海市普陀区金沙江路 1516 弄',
		          status:false
		        }],
		        status:[false,true,false,true]
			}
		},
		created(){
        	console.log(this.$refs)
    	}
	}
</script>


